 height: 8px;\n        width: 8px;\n        border-radius: 100%;\n        background: @error-color;\n        z-index: 10;\n        box-shadow: 0 0 0 1px #fff;\n    }\n\n    &-status {\n        line-height: inherit;\n        vertical-align: baseline;\n\n        &-dot {\n            width: 6px;\n            height: 6px;\n            display: inline-block;\n            border-radius: 50%;\n            vertical-align: middle;\n            position: relative;\n            top: -1px;\n        }\n        &-success {\n            background-color: @success-color;\n        }\n        &-processing {\n            background-color: @processing-color;\n            position: relative;\n            &:after {\n                position: absolute;\n                top: 0;\n                left: 0;\n                width: 100%;\n                height: 100%;\n                border-radius: 50%;\n                border: 1px solid @processing-color;\n                content: '';\n                animation: aniStatusProcessing 1.2s infinite ease-in-out;\n            }\n        }\n        &-default {\n            background-color: @normal-color;\n        }\n        &-error {\n            background-color: @error-color;\n        }\n        &-warning {\n            background-color: @warning-color;\n        }\n        &-text {\n            display: inline-block;\n            color: @text-color;\n            font-size: @font-size-small;\n            margin-left: 6px;\n        }\n    }\n}\n\n@keyframes aniStatusProcessing {\n    0% {\n        transform: scale(0.8);\n        opacity: 0.5;\n    }\n    100% {\n        transform: scale(2.4);\n        opacity: 0;\n    }\n}","@circle-prefix-cls: ~\"@{css-prefix}chart-circle\";\n\n.@{circle-prefix-cls}{\n    display: inline-block;\n    position: relative;\n\n    &-inner {\n        width: 100%;\n        text-align: center;\n        position: absolute;\n        left: 0;\n        top: 50%;\n        transform: translateY(-50%);\n        line-height: 1;\n    }\n}\n","@spin-prefix-cls: ~\"@{css-prefix}spin\";\n@spin-dot-size-small: 12px;\n@spin-dot-size: 20px;\n@spin-dot-size-large: 32px;\n\n.@{spin-prefix-cls} {\n    color: @primary-color;\n    vertical-align: middle;\n    text-align: center;\n\n    &-dot {\n        position: relative;\n        display: block;\n        border-radius: 50%;\n        background-color: @primary-color;\n        .square(@spin-dot-size);\n        animation: ani-spin-bounce 1s 0s ease-in-out infinite;\n    }\n\n    &-large &-dot {\n        .square(@spin-dot-size-large);\n    }\n\n    &-small &-dot {\n        .square(@spin-dot-size-small);\n    }\n\n    &-fix {\n        position: absolute;\n        top: 0;\n        left: 0;\n        z-index: @zindex-spin;\n        .square(100%);\n        background-color: rgba(255,255,255,.9);\n    }\n    &-fullscreen{\n        z-index: @zindex-spin-fullscreen;\n        &-wrapper{\n            position: fixed;\n            top: 0;\n            right: 0;\n            bottom: 0;\n            left: 0;\n        }\n    }\n\n    &-fix &-main {\n        position: absolute;\n        top: 50%;\n        left: 50%;\n        -ms-transform: translate(-50%, -50%);\n        transform: translate(-50%, -50%);\n    }\n\n    &-fix &-dot {\n        display: inline-block;\n    }\n\n    &-text,\n    &-show-text &-dot {\n        display: none;\n    }\n\n    &-show-text &-text {\n        display: block;\n    }\n}\n\n// use in Table loading, Table right border is not included in .ivu-table-wrapper, so fix it\n.@{table-prefix-cls}-wrapper{\n    > .@{spin-prefix-cls}-fix{\n        border: 1px solid @border-color-base;\n        border-top: 0;\n        border-left: 0;\n    }\n}\n\n@keyframes ani-spin-bounce {\n    0% {\n        transform: scale(0);\n    }\n\n    100% {\n        transform: scale(1);\n        opacity: 0;\n    }\n}\n","@alert-prefix-cls: ~\"@{css-prefix}alert\";\n@icon-prefix-cls: ~\"@{css-prefix}icon\";\n\n.@{alert-prefix-cls}{\n    position: relative;\n    padding: 8px 