        }\n\n        &.@{steps-prefix-cls}-next-error {\n            .@{steps-prefix-cls}-tail > i,\n            .@{steps-prefix-cls}-tail > i:after {\n                background-color: @error-color;\n            }\n        }\n\n        &.@{steps-prefix-cls}-custom {\n            .@{steps-prefix-cls}-head-inner {\n                background: none;\n                border: 0;\n                width: auto;\n                height: auto;\n                > .@{steps-prefix-cls}-icon {\n                    font-size: 20px;\n                    top: 2px;\n                    width: 20px;\n                    height: 20px;\n                }\n            }\n            &.@{steps-prefix-cls}-status-process {\n                .@{steps-prefix-cls}-head-inner > .@{steps-prefix-cls}-icon {\n                    color: @primary-color;\n                }\n            }\n        }\n    }\n    &-item:last-child &-tail{\n        display: none;\n    }\n\n    .@{steps-prefix-cls}-head,\n    .@{steps-prefix-cls}-main {\n        position: relative;\n        display: inline-block;\n        vertical-align: top;\n    }\n    .@{steps-prefix-cls}-head {\n        background: #fff;\n    }\n\n    .@{steps-prefix-cls}-head-inner {\n        display: block;\n        width: 26px;\n        height: 26px;\n        line-height: 24px;\n        margin-right: 8px;\n        text-align: center;\n        border: 1px solid @steps-wait-icon-color;\n        border-radius: 50%;\n        font-size: 14px;\n        transition: background-color @transition-time @ease-in-out;\n\n        > .@{steps-prefix-cls}-icon {\n            line-height: 1;\n            position: relative;\n\n            &.ivu-icon {\n                font-size: 24px;\n                &-ios-checkmark-empty,\n                &-ios-close-empty {\n                    font-weight: bold;\n                }\n            }\n        }\n    }\n    .@{steps-prefix-cls}-main {\n        margin-top: 2.5px;\n        display: inline;\n    }\n    .@{steps-prefix-c