import * as Public from '@/public/public.js'
let symbolPic='image://data:image/gif;base64,'
export default {
	backgroundColor: '#272D3A',
	tooltip: { //对应城市
		trigger: 'item',
		formatter: function (params) {
			let waringData = [0,200, 400, 600, 800]
			let waringDataArr = ['没有威胁','蓝色预警', '黄色预警', '橙色预警', '红色预警']
			let rank = Public.switchTrans(waringData, waringDataArr, params.value)
			return params.name + ' : ' + rank
		}
	},
	title: {
		text: '两广地区台风模拟',
		subtext:'作者:千茉紫依  (纯属虚构)',
		left: '10px',
		top: '10px',
		textStyle: {
			color: '#fff'
		},
	},
	visualMap: { //左下映射表
		min: 0,
		max: 800,
		left: '10px',
		bottom:'10px',
		pieces: [{
			value: 0,
			label: '没有威胁',
			color: '#ddd'
		},{
			value: 200,
			label: '蓝色预警',
			color: '#50a3ba'
		}, {
			value: 400,
			label: '黄色预警',
			color: '#ee0'
		}, {
			value: 600,
			label: '橙色预警',
			color: '#ea9736'
		}, {
			value: 800,
			label: '红色预警',
			color: '#d94e5d'
		}],
		textStyle: {
			color: '#fff'
		}
	},
	geo: { //地理坐标系,地图在vuex的southMap模块中定义
		map: '自制两广地图',
		roam: true, //可缩放移动
		left: '15%',
		top:'50px',
		label: { //地图地名样式
			normal: {
				show: false,
				fontSize: 10,
				textStyle: {
					color: 'rgba(0,0,0,0.4)'
				}
			}
		},
		itemStyle: {
			normal: {
				borderColor: 'rgba(0, 0, 0, 0.2)'
			},
			emphasis: {
				areaColor: 'rgba(255, 255, 255, 0.4)',
				shadowOffsetX: 0,
				shadowOffsetY: 0,
				shadowBlur: 20,
				borderWidth: 0,
				shadowColor: 'rgba(255, 255, 255, 0.4)'
			}
		}
	},
	series: [{
		name: 'typhoon',
		type: 'scatter',
		coordinateSystem: 'geo',
		data: [],
		zlevel: 1,
		symbolSize: (data)=>data[2]*3, //映射为params.value=[log,lat,strong]
		symbol:symbolPic,
		
		tooltip: {
			trigger: 'item',
			formatter: function (params) {
				
				let msg=`台风 ${params.data.name} <br/>生成于 ${params.data.time} <br/>目前为 ${params.data.strong}级  ${params.data.strongStr}<br/>实时位置<br/>东经: ${params.value[0]}  <br/>北纬: ${params.value[1]}`
				return msg
			}
		},
		label: {
			normal: {
				show: false,
			},
			emphasis: {
				show: true,
				backgroundColor: 'red',
				borderColor: 'white',
				borderWidth: 1,
				borderRadius:'50%',
				formatter:  function (params) {
					return  params.data.name 
				}
			}
		}
	}]
}



